/* ===========================================
   LIQUID GLASS COMPONENT CLASSES
   Apple-inspired glass material system
   =========================================== */

/* Base Glass Surface */
.glass-surface {
  position: relative;
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur-regular));
  -webkit-backdrop-filter: blur(var(--glass-blur-regular));
  border: 1px solid var(--glass-border-subtle);
  border-radius: var(--glass-radius-lg);
  box-shadow: var(--glass-shadow-ambient);
  transition: 
    transform var(--glass-transition),
    box-shadow var(--glass-transition),
    border-color var(--glass-transition),
    background var(--glass-transition);
}

.glass-surface::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: inherit;
  background: var(--glass-highlight-subtle);
  pointer-events: none;
  z-index: 1;
}

/* Elevated Glass - more prominent depth */
.glass-elevated {
  position: relative;
  background: var(--glass-bg-elevated);
  backdrop-filter: blur(var(--glass-blur-heavy));
  -webkit-backdrop-filter: blur(var(--glass-blur-heavy));
  border: 1px solid var(--glass-border-strong);
  border-radius: var(--glass-radius-lg);
  box-shadow: var(--glass-shadow-elevated);
  transition: 
    transform var(--glass-transition),
    box-shadow var(--glass-transition),
    border-color var(--glass-transition);
}

.glass-elevated::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: inherit;
  background: var(--glass-highlight);
  pointer-events: none;
  z-index: 1;
}

/* Interactive Glass - with hover/press states */
.glass-interactive {
  position: relative;
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur-regular));
  -webkit-backdrop-filter: blur(var(--glass-blur-regular));
  border: 1px solid var(--glass-border-subtle);
  border-radius: var(--glass-radius-md);
  box-shadow: var(--glass-shadow-ambient);
  cursor: pointer;
  transition: 
    transform var(--glass-transition),
    box-shadow var(--glass-transition),
    border-color var(--glass-transition),
    background var(--glass-transition);
}

.glass-interactive::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: inherit;
  background: var(--glass-highlight-subtle);
  pointer-events: none;
  z-index: 1;
  opacity: 0.5;
  transition: opacity var(--glass-transition);
}

.glass-interactive:hover {
  background: var(--glass-bg-hover);
  border-color: var(--glass-border-hover);
  box-shadow: var(--glass-shadow-hover);
  transform: translateY(-1px) scale(1.005);
}

.glass-interactive:hover::before {
  opacity: 1;
}

.glass-interactive:active {
  transform: translateY(0) scale(0.99);
  box-shadow: var(--glass-shadow-ambient);
}

/* Glass Capsule - pill shape */
.glass-capsule {
  position: relative;
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur-light));
  -webkit-backdrop-filter: blur(var(--glass-blur-light));
  border: 1px solid var(--glass-border-hairline);
  border-radius: var(--glass-radius-capsule);
  transition: 
    background var(--glass-transition),
    border-color var(--glass-transition);
}

.glass-capsule:hover {
  background: var(--glass-bg-hover);
  border-color: var(--glass-border-subtle);
}

/* Tinted Glass Variants */
.glass-tint-sage {
  background: 
    linear-gradient(to bottom, var(--glass-tint-sage), transparent),
    var(--glass-bg);
}

.glass-tint-purple {
  background: 
    linear-gradient(to bottom, var(--glass-tint-purple), transparent),
    var(--glass-bg);
}

.glass-tint-green {
  background: 
    linear-gradient(to bottom, var(--glass-tint-green), transparent),
    var(--glass-bg);
}

.glass-tint-amber {
  background: 
    linear-gradient(to bottom, var(--glass-tint-amber), transparent),
    var(--glass-bg);
}

.glass-tint-blue {
  background: 
    linear-gradient(to bottom, var(--glass-tint-blue), transparent),
    var(--glass-bg);
}

/* Glass Container - groups glass elements cohesively */
.glass-container {
  position: relative;
  background: rgba(0, 0, 0, 0.2);
  border-radius: var(--glass-radius-xl);
  padding: 2px;
}

.glass-container > * {
  position: relative;
  z-index: 1;
}

/* Glass Rail - for sidebars */
.glass-rail {
  position: relative;
  background: 
    linear-gradient(180deg, rgba(255, 255, 255, 0.02) 0%, rgba(255, 255, 255, 0.01) 100%);
  backdrop-filter: blur(var(--glass-blur-heavy));
  -webkit-backdrop-filter: blur(var(--glass-blur-heavy));
  border-right: 1px solid var(--glass-border-hairline);
}

.glass-rail::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 80px;
  background: var(--glass-highlight-subtle);
  pointer-events: none;
}

/* Legacy glass classes - updated */
.glass {
  position: relative;
  background: var(--glass-bg-elevated);
  backdrop-filter: blur(var(--glass-blur-regular));
  -webkit-backdrop-filter: blur(var(--glass-blur-regular));
  border: 1px solid var(--glass-border-subtle);
}

.glass::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: inherit;
  background: var(--glass-highlight-subtle);
  pointer-events: none;
}

.glass-light {
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur-light));
  -webkit-backdrop-filter: blur(var(--glass-blur-light));
}

/* Gradient backgrounds */
.gradient-card {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.06), rgba(255, 255, 255, 0.02));
}

.gradient-page {
  background: linear-gradient(180deg, #050505 0%, #080808 50%, #0a0a0a 100%);
}

/* Avatar gradient */
.avatar-gradient {
  background: radial-gradient(circle at 30% 30%, rgba(197, 204, 195, 0.95), rgba(168, 176, 165, 0.35));
}

/* Hover lift animation - refined */
.hover-lift {
  transition: transform var(--glass-transition), box-shadow var(--glass-transition);
}

.hover-lift:hover {
  transform: translateY(-2px);
}

/* Glass glow effect */
.glass-glow {
  position: relative;
}

.glass-glow::after {
  content: '';
  position: absolute;
  inset: -1px;
  border-radius: inherit;
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.1) 0%,
    transparent 50%,
    transparent 100%
  );
  pointer-events: none;
  opacity: 0;
  transition: opacity var(--glass-transition);
}

.glass-glow:hover::after {
  opacity: 1;
}

/* Custom scrollbar styles - refined for glass */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.08);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.15);
}

/* Fade in up animation */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(12px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-fade-in-up {
  animation: fadeInUp 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Glass shimmer animation */
@keyframes glassShimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}

.glass-shimmer::before {
  background: linear-gradient(
    90deg,
    transparent 0%,
    rgba(255, 255, 255, 0.04) 50%,
    transparent 100%
  );
  background-size: 200% 100%;
  animation: glassShimmer 8s ease-in-out infinite;
}

/* Pulse animation for indicators */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.animate-pulse-giga {
  animation: pulse 2s infinite;
}

/* Glass focus ring */
.glass-focus-ring:focus-visible {
  outline: none;
  box-shadow: 
    var(--glass-shadow-ambient),
    0 0 0 2px rgba(197, 204, 195, 0.4);
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  .glass-surface,
  .glass-elevated,
  .glass-interactive,
  .glass-capsule,
  .hover-lift {
    transition: none;
  }
  
  .glass-shimmer::before {
    animation: none;
  }
  
  .glass-interactive:hover {
    transform: none;
  }
}
